.pusher{
    transition: all 0.5s linear !important;
}
#root{
    -ms-overflow-style: none;  /* IE and Edge */
    scrollbar-width: none;  /* Firefox */
}
#root::-webkit-scrollbar {
    display: none;
}
.no-scroll{
    -ms-overflow-style: none;  /* IE and Edge */
    scrollbar-width: none;  /* Firefox */
}
.no-scroll::-webkit-scrollbar {
    display: none;
}